import * as React from "react"
import { Link } from "gatsby"
import { ChevronRight } from "lucide-react"

const CourseBreadcrumb = ({ courseTitle, moduleTitle, courseSlug }) => {
    return (
        <nav aria-label="Breadcrumb" className="mb-6">
            <ol className="flex items-center gap-2 flex-wrap" style={{ fontSize: "var(--text-sm)" }}>
                <li>
                    <Link
                        to="/"
                        className="text-[var(--color-editorial-grey)] hover:text-[var(--color-editorial-accent)] transition-colors"
                    >
                        Home
                    </Link>
                </li>
                <li>
                    <ChevronRight className="w-4 h-4 text-[var(--color-editorial-grey)]" />
                </li>
                <li>
                    <Link
                        to="/courses/"
                        className="text-[var(--color-editorial-grey)] hover:text-[var(--color-editorial-accent)] transition-colors"
                    >
                        Courses
                    </Link>
                </li>
                {courseTitle && (
                    <>
                        <li>
                            <ChevronRight className="w-4 h-4 text-[var(--color-editorial-grey)]" />
                        </li>
                        <li>
                            {moduleTitle ? (
                                <Link
                                    to={`/courses/${courseSlug}/`}
                                    className="text-[var(--color-editorial-grey)] hover:text-[var(--color-editorial-accent)] transition-colors"
                                >
                                    {courseTitle}
                                </Link>
                            ) : (
                                <span className="text-[var(--color-editorial-black)]" style={{ fontWeight: 600 }}>
                                    {courseTitle}
                                </span>
                            )}
                        </li>
                    </>
                )}
                {moduleTitle && (
                    <>
                        <li>
                            <ChevronRight className="w-4 h-4 text-[var(--color-editorial-grey)]" />
                        </li>
                        <li>
                            <span className="text-[var(--color-editorial-black)]" style={{ fontWeight: 600 }}>
                                {moduleTitle}
                            </span>
                        </li>
                    </>
                )}
            </ol>
        </nav>
    )
}

export default CourseBreadcrumb
